<template>
  <div class="wiki-mine-message">
    <el-table :data="tableData" style="width: 100%" max-height="500">
      <el-table-column fixed prop="comment_time" label="日期" width="200"></el-table-column>
      <el-table-column prop="comment_username" label="用户名" width="120"></el-table-column>
      <el-table-column prop="comment_article" label="文章" width="130"></el-table-column>
      <el-table-column prop="content" label="内容" width="600"></el-table-column>
      <el-table-column fixed="right" label="操作" width="120">
        <template slot-scope="scope" width="120">
          <el-button
            @click.native.prevent="seecomment(scope.$index, tableData)"
            type="text"
            size="small"
          >查看评论</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="prev, pager, next"
      :total="pageNum"
      :page-size="8"
      @current-change="togglePage"
    ></el-pagination>
  </div>
</template>

<script>
import * as ajax from '@/api'
import { mapMutations } from "vuex";

export default {
  created() {
    ajax.getMyCommentList(1).then(resp => {
      if (resp.data.code) {
        this.tableData = resp.data.data.comment_info;
        this.pageNum = resp.data.data.page_info.totalPage * 8;
      }
    });
  },
  data() {
    return {
      tableData: [],
      pageNum: 8
    };
  },
  methods: {
    ...mapMutations(["commentmodal_true"]),
    seecomment(index, data) {
      this.commentmodal_true(data[index]);
    },
    togglePage(currentPage) {
      ajax.getMyCommentList(currentPage).then(resp => {
        if (resp.data.code) {
          this.tableData = resp.data.data.comment_info;
          this.pageNum = resp.data.data.page_info.totalPage * 8;
        }
      });
    }
  }
};
</script>

<style lang='scss'>
.wiki-mine-message {
  .el-table {
    .cell {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    .el-table__body-wrapper {
      overflow-y: hidden;
    }
    max-height: 100% !important;
    .el-table__body-wrapper {
      max-height: 100% !important;
    }
  }
  .el-pagination{
    text-align: center;
    margin-top: 20px;
  }
}
</style>

